                              </tr>
                              <tr>
                                <td></td>
                                <td colspan="2" class="ColorRed">{{B_MaxMoneyA}}</td>
                                <td colspan="2" class="ColorRed">{{B_MaxMoneyB}}</td>
                                <td colspan="2" class="ColorRed">{{B_MaxMoneyC}}</td>
                              </tr>
                              <tr>
