                  </ol>
                  <ol class="fl">
                    <li>
                      <span class="ColorRed">{{BaseScoreRule.rate_score}}</span>
                      =<span class="ColorRed">{{BaseScoreRule.rate_money}}</span></li>
                    <li><span class="ColorRed">{{BaseScoreRule.score_register}}</span></li>
                    <li><span class="ColorRed">{{BaseScoreRule.score_login}}</span></li>
