}
.rule-box{ overflow: hidden;padding-bottom: 50px;}
.rule-box ul{
  margin-left: 26px;
  margin-top: 34px;
}
.mark-bar:before{
  content: '';
  position: absolute;
  top: 3px;
  left: 0;
  width: 4px;
  height: 20px;
  background: #FFC156;
  -webkit-border-radius: 100px;
  -moz-border-radius: 100px;
  border-radius: 100px;
}
.mark-bar{
  font-size: 18px;
  font-weight: 600;
  color: #333333;
  padding-left: 15px;
  margin-bottom: 17px;
  position: relative;
  margin-top: 50px;
}
.rule-box{
  min-height: 572px;
  height: auto;
  background-color: #fff;
}




</style>



